import { BrowserModule } from '@angular/platform-browser';
import { NgModule ,Component, ApplicationRef } from '@angular/core';
import { FormsModule ,ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { RouterModule, Routes } from '@angular/router';
import { AppComponent } from './app.component';
import { HomeComponent } from './components/home/home.component';
import { TripComponent } from './components/trip/trip.component';
import { NavbarComponent } from './components/navbar/navbar.component';
import { ProfileComponent } from './components/profile/profile.component';
import { AlertComponent } from './directives/alert/alert.component';
import { AgmCoreModule } from '@agm/core';
import { CommonModule } from '@angular/common';
import { HttpClientModule } from '@angular/common/http';
import { ResturantListComponent } from './components/resturant-list/resturant-list.component';
import { ResturantListService } from './services/resturant-list.service';
import { ContactsService } from './services/contacts.service';
import { AlertService } from './services/alert.service';
import { ContactsComponent } from './components/contacts/contacts.component';
import { SharedModule } from './shared/shared.module';
import { RecaptchaModule } from 'ng-recaptcha';
import { GoogleMapDirective } from './directives/google-map.directive';
import { TutorialComponent } from './components/tutorial/tutorial.component';
import { FooterComponent } from './components/footer/footer.component';
import { TreeViewComponent } from './components/tree-view/tree-view.component';
import { TestComponent } from './components/test/test.component';
import { TreeModule } from 'angular-tree-component';
import {MyNewDirectiveDirective} from "./directives/my-new-directive.directive";

const appRoutes: Routes =  [
  {path:'', component: HomeComponent},
  {path:'Trip', component: TripComponent},
  {path:'Profile', component: ProfileComponent},
  {path:'Contacts', component: ContactsComponent},
  {path:'Tutorial', component: TutorialComponent},
  {path:'Test', component: TestComponent},
]

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    TripComponent,
    NavbarComponent,
    ProfileComponent,
    ResturantListComponent,
    ContactsComponent,
    AlertComponent,
    GoogleMapDirective,
    TutorialComponent,
    FooterComponent,
    TreeViewComponent,
    TestComponent,
    MyNewDirectiveDirective
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    HttpClientModule,
    CommonModule,
    ReactiveFormsModule,
    RouterModule.forRoot(appRoutes),
    RecaptchaModule.forRoot(),
    AgmCoreModule.forRoot({
      apiKey: 'AIzaSyDFRsRIKxzaSLI-2njS5xXbdo-MfHnb5-o',
      libraries: ['places']
    }),
    SharedModule,
    TreeModule,
  ],

  providers: [AppComponent, HttpModule, ResturantListService, AlertService, ContactsService, GoogleMapDirective ],
  bootstrap: [AppComponent],

})
export class AppModule { }



