import { Injectable, Input} from '@angular/core';
import { Http, Headers, RequestOptions, Response } from '@angular/http';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/filter';
import 'rxjs/add/operator/catch';
import { Observable } from 'rxjs/Rx';
import { ContactSubmission } from '../models/contact-submission';


@Injectable()
export class ContactsService {
  contactSubmission : ContactSubmission [];
  constructor(private http: Http ) {}
  
  sendContactMsg(contactSubmission){
      let headers = new Headers();
      headers.append('Content-Type', 'application/json');
      let requestOpt = new RequestOptions({ headers: headers});
      return this.http.post('api/contacts/sendContactMsg', contactSubmission, requestOpt)
          .map(res => res.json());
    }
     
     private jwt() {
        // create authorization header with jwt token
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        if (currentUser && currentUser.token) {
            let headers = new Headers({ 'Authorization': 'Bearer ' + currentUser.token });
            return new RequestOptions({ headers: headers });
        }
    }     

}



